import from_index from '../index.js';

let state = '';

            let standing_left_frame = 0;
            let standing_right_frame = 0;

            let walking_left_frame = 0;
            let walking_right_frame = 0;
            
            
let Handle_ui = () => {

    state = from_index.Handle_return_state();

    // right
    if ( state.ui.interaction.player_1.stance == 5 ) {

        // standing right loop
        if ( state.ui.interaction.player_1.idle.standing
            && !state.ui.interaction.player_1.cycles.walking ) {
                
                if (document.getElementById('player_svg') != null) {
                    document.getElementById('player_svg').innerHTML = '';

                        document.getElementById('player_svg').classList = ('nownigel_gui_interaction_man_0_16_depth_200_face_0_breathing_right_'+standing_right_frame+ ' float_right position_absolute bottom_0 right_0 width_100 height_100 margin_auto ');

                    if (standing_right_frame == 4) {
                        standing_right_frame = 0;
                    };
                    standing_right_frame += 1;
                };

        };

        // walking right loop
        if ( state.ui.interaction.player_1.idle.standing
            && state.ui.interaction.player_1.cycles.walking ) {

                if (document.getElementById('player_svg') != null) {
                    document.getElementById('player_svg').innerHTML = '';

                        document.getElementById('player_svg').classList = ('nownigel_gui_interaction_man_0_16_depth_200_face_0_walk_right_'+walking_right_frame+ ' float_right position_absolute bottom_0 right_0 width_100 height_100 margin_auto ');

                    if (walking_right_frame == 9) {
                        walking_right_frame = 0;
                    };
                    walking_right_frame += 1;
                };

        };

    };

    // left
    if ( state.ui.interaction.player_1.stance == 13 ) {

        // standing left loop 
        if ( state.ui.interaction.player_1.idle.standing
            && !state.ui.interaction.player_1.cycles.walking ) {
                
                if (document.getElementById('player_svg') != null) {
                    document.getElementById('player_svg').innerHTML = '';

                        document.getElementById('player_svg').classList = ('nownigel_gui_interaction_man_0_16_depth_200_face_0_breathing_left_'+standing_left_frame+ ' float_left position_absolute bottom_0 right_0 width_100 height_100 margin_auto ');

                    if (standing_left_frame == 4) {
                        standing_left_frame = 0;
                    };
                    standing_left_frame += 1;
                };

        };

        // walking left loop
        if ( state.ui.interaction.player_1.idle.standing
            && state.ui.interaction.player_1.cycles.walking ) {

                if (document.getElementById('player_svg') != null) {
                    document.getElementById('player_svg').innerHTML = '';

                        document.getElementById('player_svg').classList = ('nownigel_gui_interaction_man_0_16_depth_200_face_0_walk_left_'+walking_left_frame+ ' float_left position_absolute bottom_0 right_0 width_100 height_100 margin_auto ');

                    if (walking_left_frame == 9) {
                        walking_left_frame = 0;
                    };
                    walking_left_frame += 1;
                };

        };

    };
    
};

export default {
    Handle_ui
}